
import Datatang from "../..//public/lib/meek"
let currentDrawTool,
  crowssLayer,
  horizontalLine,
  verticalLine,
  referTo,
  dataExtent,
  xmax,
  ymax,
  currentMap,
  detectArea;


export function initCross(map, drawTool, config) {
  if (map) {
    currentMap = map
    map.addEventListener(Datatang.BrowserEvent.MOUSE_MOVE, handleMouseMoveForDrawingCrowss)
    currentDrawTool = drawTool
    crowssLayer = new Datatang.FeatureLayer()
    map.addLayer(crowssLayer)
    horizontalLine = new Datatang.Line([])
    verticalLine = new Datatang.Line([])
    referTo = new Datatang.Extent()
    let lineStyle = new Datatang.LineStyle([255, 0, 0], 1, 1, Datatang.LineStyle.LineCap.ROUND, Datatang.LineStyle.LineJion.ROUND)
    let fillStyle = new Datatang.FillStyle([255, 0, 0], lineStyle, 0)
    let referTofeature = new Datatang.Feature(referTo, {}, '', [fillStyle]);
    crowssLayer.addFeatures([new Datatang.Feature(horizontalLine, {}, '', [lineStyle]),
    new Datatang.Feature(verticalLine, {}, '', [lineStyle])])
    if (config) {
      detectArea = config.split("*");
      crowssLayer.addFeature(referTofeature);
    }
    crowssLayer.visible = false
    dataExtent = map.view.dataExtent
    xmax = dataExtent[2]
    ymax = dataExtent[3]
  }
}


function handleMouseMoveForDrawingCrowss(e) {
  if (currentDrawTool) {

    if (currentDrawTool.drawMode === Datatang.Draw.DrawMode.EXTENT && currentDrawTool.active == true) {
      if (crowssLayer.visible === false) {
        crowssLayer.visible = true
      }

      let coordinate = e.coordinate
      let x = coordinate[0],
        y = coordinate[1]

      if (x < 0) {
        x = 0
      }

      if (y < 0) {
        y = 0
      }

      if (x > xmax) {
        x = xmax
      }

      if (y > ymax) {
        y = ymax
      }

      var hLineCoords = [[0, y], [xmax, y]]
      var vLineCoords = [[x, 0], [x, ymax]]

      horizontalLine.setCoordinates(hLineCoords)
      verticalLine.setCoordinates(vLineCoords)
      if (detectArea) {
        console.log("detectArea", detectArea)
        let dx = Number(detectArea[0]);
        let dy = Number(detectArea[1]);
        referTo.setCoordinates([[x - dx, y - dy], [x + dx, y - dy], [x + dx, y + dy], [x - dx, y + dy]])
      }

    } else {
      crowssLayer.visible = false;
      currentMap.render();
    }
  }
}


