<!-- 标签列表 组件 -->

<template>
  <div class="tag-list">
    <div class="head">
      <span>标签列表</span>
    </div>
    <ul ref="tag">
      <li v-for="(item ,index) in computedTagList" :key="index" @click="click(item,index)" :class="{'active':index==isClickIndex}">
        <u :style="{backgroundColor:'rgb('+(item.fillColor)+')'}"></u>
        <span>{{item.tagValue}}</span>
        <i @click.stop="hideOrShow(item,$event)" class="icon-eye" :style="{backgroundImage:'url('+require('../assets/images/u25.png')+')'}"></i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isClickIndex: 0
    };
  },
  created() {},
  mounted() {
    let el = this.$refs.tag.children;
    document.addEventListener("keydown", e => {
      if (e.code === "Tab") {
        //9是tap
        e.preventDefault();
        if (this.isClickIndex === el.length - 1) {
          this.isClickIndex = -1;
        }
        el[this.isClickIndex + 1].click();
      }
    });
    el[this.isClickIndex].click();
  },
  props: {
    tagList: {
      type: Array
    }
  },
  computed: {
    computedTagList() {
      let cache = this.tagList.map(element => {
        element.isShow = true;
        return element;
      });
      return cache;
    }
  },
  methods: {
    click(item, index) {
      this.$emit("itemVal", item);
      this.isClickIndex = index;
    },
    hideOrShow(item, event) {
      event.target.style.backgroundImage = item.isShow
        ? "url(" + require("../assets/images/u27.png") + ")"
        : "url(" + require("../assets/images/u25.png") + ")";
      event.target.style.backgroundPosition = item.isShow ? "0 0" : "0 4px";
      this.$emit("hideOrShow", item);
      item.isShow = !item.isShow;
    }
  }
};
</script>

<style scoped>
.tag-list {
  width: 236px;
  position: absolute;
  top: 80px;
  left: 150px;
  color: white;
}
.tag-list ul {
  background: rgb(0, 18, 44, 0.2);
}
.tag-list li {
  padding: 10px 10px;
}
.tag-list li.active {
  background: rgba(55, 65, 220, 0.2);
}
.tag-list ul li u {
  display: inline-block;
  height: 15px;
  width: 15px;
  border-radius: 50%;
}
.head {
  text-align: center;
  height: 30px;
  background: rgba(49, 62, 84, 0.2);
  border: 1px solid transparent;
  padding-top: 8px;
}
.icon-eye {
  display: block;
  width: 26px;
  height: 20px;
  background: no-repeat;
  background-size: 25px auto;
  background-position: 0 4px;
  float: right;
  margin-top: -4px;
}
</style>



