<!-- 标签列表 组件 -->

<template>
  <div class="dt-popup">
    <div class="head">
      属性编辑
    </div>
    <span class="close" @click="cancel">
      X
    </span>
    <div>
      <div v-if="isShowMode">
        <p class="title">边框质检</p>
        <span>
          边框不贴合：
          <input type="checkbox" id="topBorder" value="top" v-model="quality.errorType.unfit">
          <label for="topBorder ">上边框</label>
          <input type="checkbox" id="bottomBorder" value="bottom" v-model="quality.errorType.unfit">
          <label for="bottomBorder ">下边框</label>
          <input type="checkbox" id="rightBorder" value="right" v-model="quality.errorType.unfit">
          <label for="rightBorder">右边框</label>
          <input type="checkbox" id="leftBorder" value="left" v-model="quality.errorType.unfit">
          <label for="leftBorder">左边框</label>
        </span>
      </div>
      <div v-if="isShowMode">
        <span style="margin-left:14px;">目标错误：
          <input type="checkbox" id="labelingFor" value="labelingFor" v-model="quality.errorType.targetError">
          <label for="labelingFor">标注条件不符</label>
          <input type="checkbox" id="annotationObject" value="annotationObject" v-model="quality.errorType.targetError">
          <label for="annotationObject">标注对象不符</label>
          <input type="checkbox" id="other" value="other" v-model="quality.errorType.targetError">
          <label for="other">其他</label>
        </span>
      </div>
    </div>
    <p class="title" v-if="isShowMode">属性质检</p>
    <ul class="property" :style="!isShowMode?'margin:20px auto;':''">
      <li v-for="(item, index) in formList" :key="index">
        <div class="left">
          <span>{{item.propertyName}}: </span>
          <span v-if="item.type==='text'">
            <input type="text" @input="input" v-model="values[item.propertyKey]">
            <!-- <span>该项错误</span><input type="checkbox" :value="item.propertyKey" v-model="quality.errorType.attributeError"> -->
          </span>
          <span>
            <span v-for="(value, idx) in item.propertyValuelst" :key="idx">
              <input :type="item.type" :id="'lable'+index+''+idx" :value="value.propertyValue" v-model="values[item.propertyKey]" @input="input">
              <label :for="'lable'+index+''+idx">{{value.propertyLabel}}</label>
            </span>
          </span>
        </div>
        <div class="right" v-if="isShowMode">
          <span>该项错误</span><input type="checkbox" :value="item.propertyKey" v-model="quality.errorType.attributeError">
        </div>
      </li>
    </ul>
    <div>
      <div class="footer" v-if="!isShowMode">
        <button class="btn blue" @click="click">确定</button>
      </div>
      <div class="footer" v-if="isShowMode">
        <button class="btn red" @click="qualifiedSure">不合格</button>
        <button class="btn green" @click="qualified">合格</button>
      </div>
    </div>
  </div>
</template>

<script>
import { ModeType } from "../utils/enum.js";
import Data from "../utils/dt-core-lib.js";
let mode = Data.getMode();
/* eslint-disable */
export default {
  data() {
    return {
      cache: {},
      qualityCache: {},
      disabled: false,
      isShowQualify: false,
      isShowMode: false
    };
  },
  created() {
    if (mode == ModeType.ZHIJIAN) {
      this.isShowMode = true;
      this.disabled = true;
    }
  },
  props: {
    formList: {
      type: Array
    },
    qualifiedResult: {
      type: Object
    },
    isShowForm: {
      type: Boolean
    },
    result: {
      type: Object
    }
  },
  computed: {
    values() {
      this.formList.forEach(element => {
        if (element.type == "checkbox") {
          this.cache[element.propertyKey] =
            this.result[element.propertyKey] || [];
        } else {
          this.cache[element.propertyKey] =
            this.result[element.propertyKey] || "";
        }
      });
      this.cache = Object.assign({}, this.result, this.cache);
      return this.cache;
    },
    quality() {
      this.qualityCache = this.qualifiedResult;
      if (!this.qualifiedResult.errorType) {
        this.qualityCache.errorType = {
          unfit: [],
          targetError: [],
          attributeError: []
        };
      }
      console.log("this.qualityCache", this.qualityCache);
      return this.qualityCache;
    }
  },
  watch: {
    isShowForm(newVal) {
      if (newVal) {
        this.isShowQualify = false;
      }
    }
  },
  methods: {
    input() {
      // this.$emit("formVal", this.values);
    },
    click() {
      this.$emit("submitForm", this.values);
    },
    cancel() {
      this.$emit("cancel");
    },
    qualified() {
      this.$emit("qualified");
    },
    qualifiedSure() {
      if (this.quality.length === 0) {
        alert("请选择错误类型");
        return;
      }
      console.log("this.quality.errType", this.quality.errorType);

      this.$emit("qualifiedSure", this.quality.errorType);
    }
  }
};
</script>

<style scoped>
.dt-popup {
  width: 550px;
  background: white;
  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 10px;
  padding: 0 20px;
}
.head {
  font-size: 16px;
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}
.property {
  margin-bottom: 20px;
  width: 300px;
  overflow: hidden;
}
.footer {
  text-align: center;
}
.qualify {
  padding-top: 30px;
  padding-bottom: 20px;
  overflow: hidden;
  padding-left: 120px;
}
.qualify div {
  float: left;
}
.btn {
  height: 40px;
  width: 80px;
  border-radius: 8px;
  color: white;
}
.blue {
  background: rgb(25, 136, 204);
}
.green {
  background: rgb(40, 127, 36);
}
.red {
  background: rgb(224, 17, 32);
}
.close {
  display: block;
  position: absolute;
  top: 4px;
  right: 4px;
  cursor: pointer;
}
.title {
  font-weight: bold;
  margin-left: -8px;
}
.left {
  width: 72%;
  float: left;
}
.right {
  width: 28%;
  float: left;
}
</style>



