<!-- 图形列表 组件 -->

<template>
  <div class="shape-list">
    <div class="head">
      <span>图形列表</span>
    </div>
    <ul class="title">
      <li>
        <span>ID</span>
        <span>标签</span>
      </li>
    </ul>
    <ul>
      <li v-for="(item ,index) in shapeList" :key="index">
        <span>{{item.id}}</span>
        <span>{{item.tagValue}}</span>
        <span class="icon-del" :style="{backgroundImage:'url('+require('../assets/images/u101.png')+')'}" @click="click(item)"></span>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapActions } from "vuex";
export default {
  data() {
    return {};
  },
  created() {},
  computed: {
    ...mapState("shapeList", ["shapeList"])
  },
  methods: {
    ...mapActions("shapeList", ["delete"]),
    click(item) {
      this.delete(item);
    }
  }
};
</script>

<style scoped>
.shape-list {
  width: 200px;
  position: absolute;
  top: 200px;
  right: 150px;
  color: white;
}
.shape-list ul {
  background: rgba(0, 18, 44, 0.2);
  max-height: 200px;
}
.shape-list li {
  padding: 10px 10px;
}
.shape-list ul:last-child {
  overflow-y: scroll;
}
.shape-list ul li u {
  display: inline-block;
  height: 15px;
  width: 15px;
  border-radius: 50%;
}
.head {
  text-align: center;
  height: 30px;
  background: rgba(49, 62, 84, 0.2);
  border: 1px solid transparent;
  padding-top: 8px;
}
.icon-del {
  display: block;
  width: 26px;
  height: 20px;
  background: no-repeat;
  background-size: 25px auto;
  background-position: 0 -1px;
  float: right;
}
/* .title {
  border-bottom: 1px solid red;
} */
</style>



