<template>
  <div id="app">
    <div id="map" style="width:100%;height:100%;background:#b5d0d0">
    </div>
    <form-list ref="popup" :formList="formList" :qualifiedResult="qualifiedResult" @submitForm="submit" @cancel="cancel" @qualified="qualified" @qualifiedSure="qualifiedSure" v-show="isShowForm" :isShowForm="isShowForm" :result="result"></form-list>
    <tag-list :tagList='tagList' @itemVal="itemVal" @hideOrShow="hideOrShow" v-drag ref="tag"></tag-list>
    <shape-list v-drag></shape-list>
    <tool-bar @select="select" @drawRect="drawRect" @zoomIn="zoomIn" @zoomOut="zoomOut" @reset="reset" @shapeHide="shapeHide" @lableHide="lableHide" @goHome="goHome" @currentShape="currentShape" @and="and"></tool-bar>
  </div>
</template>

<script>
import TagList from "@/components/tagList";
import ShapeList from "@/components/shapeList";
import FormList from "@/components/formList";
import ToolBar from "@/components/toolBar";
import Datatang from "../public/lib/meek.js";
import { getUid } from "@/utils/uuid";
import { deepClone, getImageInfo } from "@/utils/util";
import { initCross } from "@/utils/cross.js";
import Data from "@/utils/dt-core-lib";
import { mapActions } from "vuex";
import { ModeType } from "@/utils/enum";
import store from "@/store";
const extent = [0, 0, 1049, 768];
let flayer = new Datatang.FeatureLayer();
let singleImageLayer = new Datatang.SingleImageLayer({
  url: "",
  imageExtent: extent,
  projection: {
    extent: extent
  }
});
let drawTool = new Datatang.Draw({
  type: "extent",
  drawLayer: flayer,
  finishCondition(e) {
    if (e.code === "Enter") return true;
  },
  undoCondition(e) {
    if (e.code === "Escape") return true;
  }
});
let select = new Datatang.Select({
  selectMultiMode(event) {
    return event.keyCode == 17;
  }
});
select.active = false;
let modifyTool = new Datatang.Modify();
let map = null;
let overlay = null;
let originalResolution = null;
let configList = null;

let mode = null; //模式
export default {
  name: "app",
  data() {
    return {
      isShowForm: false,
      currentFeature: null,
      selectedFeatures: null,
      tagList: [],
      formList: [],
      tagInfo: {}, //标签
      uuid: null, //index
      result: {}, //表单结果,
      qualifiedResult: {} //质检结果
    };
  },
  components: { TagList, FormList, ShapeList, ToolBar },
  created() {
    this.tagList = Data.getTagListData(); //从解析库获取tagList
    this.formList = Data.getFormListData(); //从解析库获取formList
    configList = Data.getFoundationConfig(); //从解析库获取全局配置
    console.log("Config", Data.getFoundationConfig());
    mode = Data.getMode(); //从解析库获取当前模式
  },
  mounted() {
    document.oncontextmenu = function() {
      return false;
    };
    let that = this;
    overlay = new Datatang.Overlay({
      element: this.$refs.popup.$el,
      autoPan: true
    });
    map = new Datatang.Map({
      layers: [singleImageLayer, flayer],
      overlays: [overlay],
      target: "map",
      view: new Datatang.View({
        projection: {
          extent: extent
        },
        center: Datatang.ExtentUtil.getCenter(extent),
        zoom: 2,
        maxZoom: 8,
        resolution: 1
      })
    });

    // 绘图工具
    map.addComponents(drawTool);
    map.addComponents(select);
    map.addComponents(modifyTool);
    drawTool.addEventListener(
      Datatang.DrawEvent.EventType.DRAW_END,
      drawEvent => {
        let feature = drawEvent.feature;
        let geometry = feature.geometry;
        let pixLimit = configList.PixLimit.split("*");
        if (geometry.width < pixLimit[0] || geometry.height < pixLimit[1]) {
          alert(`图形不能小于${configList.PixLimit}像素`);
          flayer.removeFeature(feature);
          return;
        }
        overlay.position = geometry.getFormShowPosition();
        that.currentFeature = feature;
        let uuid = getUid(flayer);
        this.currentFeature.set("objectId", uuid);
        that.isShowForm = true;
      }
    );
    // flayer.addEventListener(
    //   Datatang.FeatureEvent.EventType.FEATURE_COLLECTION_CHANGED,
    //   e => {
    //     console.log(e);
    //   }
    // );
    select.addEventListener(
      Datatang.SelectEvent.EventType.SELECT,
      selectEvent => {
        //鼠标右键
        if (selectEvent.browserEvent.originalEvent.button === 2) {
          this.currentFeature = selectEvent.selectedFeatures[0];
          let geometry = this.currentFeature.geometry;
          this.result = this.currentFeature.get("content");
          this.qualifiedResult = this.currentFeature.get("quality");
          console.log("this.qualifiedResult1", this.qualifiedResult);
          overlay.position = geometry.getFormShowPosition();
          that.isShowForm = true;
        }
        that.selectedFeatures = selectEvent.selectedFeatures;
        modifyTool.features = selectEvent.selectedFeatures;
      }
    );
    if (mode === ModeType.ZHIJIAN) {
      drawTool.active = false;
      select.active = true;
    }
  },
  computed: {
    displayText() {
      return (
        this.currentFeature.get("objectId") +
        "-" +
        this.tagInfo.propertyName +
        "-" +
        this.formList[0].propertyName +
        " " +
        this.currentFeature.get("content")[this.formList[0].propertyKey]
      );
    }
  },
  methods: {
    ...mapActions("shapeList", ["add", "modify"]),
    itemVal(item) {
      this.tagInfo.propertyName = item.tagValue;
      this.tagInfo.propertyKey = item.tagKey;
      const style = [
        new Datatang.FillStyle(
          item.fillColor,
          new Datatang.LineStyle(item.boxColor, item.boxColorOpacity, 1.25),
          item.fillColorOpacity
        )
      ];
      flayer.style = () => {
        return style;
      };
      if (this.selectedFeatures) {
        this.selectedFeatures.forEach(feature => {
          feature.set("_originStyle", style);
          feature.style = style;
          feature.displayText =
            feature.get("objectId") +
            "-" +
            item.tagValue +
            "-" +
            this.formList[0].propertyName +
            " " +
            feature.get("content")[this.formList[0].propertyKey];
          feature.set("label", this.tagInfo);
          this.modify({
            tagValue: item.tagValue,
            features: this.selectedFeatures
          });
        });
        map.render();
      }
    },
    select() {
      drawTool.active = false;
      select.active = true;
    },
    drawRect() {
      this.selectedFeatures = [];
      modifyTool.features = [];
      drawTool.active = true;
      select.active = false;
    },
    and() {
      if (this.selectedFeatures && this.selectedFeatures.length >= 2) {
        this.selectedFeatures.forEach(feature => {
          feature.set("assembleId", 1);
        });
      } else {
        alert("请先选择要组合的图形");
      }
    },
    zoomIn() {
      const currentResolution = map.view.resolution;
      if (currentResolution) {
        // const newResolution = map.view.constrainResolution(
        //   currentResolution,
        //   -1
        // );
        map.view.resolution = currentResolution + configList.ScalePercent / 100;
      }
    },
    zoomOut() {
      const currentResolution = map.view.resolution;
      if (currentResolution) {
        // const newResolution = map.view.constrainResolution(
        //   currentResolution,
        //   1
        // );
        map.view.resolution = currentResolution - configList.ScalePercent / 100;
      }
    },
    reset() {
      flayer.clear();
      this.currentFeature = null;
      this.selectedFeatures = null;
      store.dispatch("shapeList/clear");
    },
    currentShape() {
      if (this.selectedFeatures && this.selectedFeatures.length === 1) {
        flayer.features.forEach(feature => {
          feature.display = false;
        });
        this.selectedFeatures[0].display = true;
      }
    },
    shapeHide(flag) {
      flayer.features.forEach(feature => {
        flag ? (feature.display = false) : (feature.display = true);
      });
    },
    lableHide(flag) {
      flayer.features.forEach(feature => {
        flag ? (feature.textDisplay = false) : (feature.textDisplay = true);
      });
    },
    goHome() {
      map.view.center = map.view.originalCenter;
      map.view.resolution = originalResolution;
    },
    submit(content) {
      let clonee = deepClone(content); //深度克隆对象
      let tagInfo = deepClone(this.tagInfo);
      this.currentFeature.set("content", clonee);
      this.currentFeature.set("quality", {});
      this.currentFeature.set("label", tagInfo);
      this.currentFeature.displayText = this.displayText;
      this.isShowForm = false;
      map.render();
      this.result = {};
      this.add({
        shapeInfo: {
          id: this.currentFeature.get("objectId"),
          tagValue: this.tagInfo.propertyName,
          currentFeature: this.currentFeature
        },
        flayer
      });
      console.log(Datatang.GeoJSON.write(flayer.features));
      this.currentFeature = null;
    },
    qualified() {
      this.currentFeature.set("quality", {
        qualityStatus: "passed" //单框质检状态 passed，failed
      });
      this.isShowForm = false;
    },
    qualifiedSure(data) {
      this.currentFeature.set("quality", {
        qualityStatus: "failed", //单框质检状态 passed，failed
        errorType: data //错误类型（内部已经定义），只有单框质检状态为failed时，才有此字段。attribute：属性错误；unfit：图形不贴合；wrongmark：错标
      });
      const style = [
        new Datatang.FillStyle(
          [255, 0, 0],
          new Datatang.LineStyle([255, 0, 0], 1, 1.25),
          0.2
        )
      ];
      this.currentFeature.set("_originStyle", style);

      this.currentFeature.style = style;
      this.isShowForm = false;
    },
    cancel() {
      flayer.removeFeature(this.currentFeature);
      this.isShowForm = false;
    },
    hideOrShow(item) {
      flayer.features.forEach(element => {
        if (element.get("label").propertyName == item.tagValue) {
          if (item.isShow) {
            element.display = false;
          } else {
            element.display = true;
          }
        }
      });
    }
  }
};
//导出全局方法
if (!window.Template) {
  window.Template = {
    setTemplateData(markResult, imageUrl) {
      getImageInfo(imageUrl).then(result => {
        flayer.clear();
        store.dispatch("shapeList/clear");
        document.getElementById("map").innerHTML = "";
        let extent = [0, 0, result.width, result.height];
        drawTool._dataExtent = extent;
        let singleImageLayer = new Datatang.SingleImageLayer({
          url: imageUrl,
          imageExtent: extent,
          projection: {
            extent: extent
          }
        });
        map = new Datatang.Map({
          layers: [singleImageLayer, flayer],
          overlays: [overlay],
          target: "map",
          view: new Datatang.View({
            projection: {
              extent: extent
            },
            center: Datatang.ExtentUtil.getCenter(extent),
            zoom: 2,
            maxZoom: 8,
            resolution: 1,
            resolutions: [8, 4, 2, 1.1764, 1, 0.5, 0.25, 0.125]
          })
        });
        let mapW = map.viewport.getBoundingClientRect().width;
        let mapH = map.viewport.getBoundingClientRect().height;
        if (result.width < result.height) {
          originalResolution = map.view.resolution = result.width / mapW;
        } else {
          originalResolution = map.view.resolution = result.height / mapH;
        }
        if (configList.CrossReferenceLine) {
          initCross(map, drawTool, configList.DetectArea);
        }
        map.addComponents(drawTool);
        map.addComponents(select);
        map.addComponents(modifyTool);
        if (markResult) {
          let features = Datatang.GeoJSON.read(markResult);
          flayer.addFeatures(features);
          features.forEach(feature => {
            let item = feature.get("label").propertyKey;
            Data.getTagListData().forEach(element => {
              if (item == element.tagKey) {
                const style = [
                  new Datatang.FillStyle(
                    element.fillColor,
                    new Datatang.LineStyle(
                      element.boxColor,
                      element.boxColorOpacity,
                      1.25
                    ),
                    element.fillColorOpacity
                  )
                ];
                feature.style = style;
              }
            });
          });
          store.dispatch("shapeList/init", {
            features,
            flayer
          });
        }
      });
    },
    getMarkResult() {
      let markResult = Datatang.GeoJSON.write(flayer.features);
      return JSON.stringify(markResult);
    }
  };
}
</script>

<style>
#app {
  position: relative;
}
</style>



