import { ModeType } from './enum';
export default {
  //获取模式
  getMode() {
    let mode = null;
    switch (Number(window.getData.mode)) {
      case 1:
        mode = ModeType.BIAOZHU;
        break;
      case 2:
        mode = ModeType.ZHIJIAN;
        break;
    }
    return mode;
  },
  //获取标签配置数据
  getTagListData() {
    let dataArrary = [];
    window.getData.formData.propertyLst[0].propertyValuelst.forEach(element => {
      let data = {
        boxColor: element.boxColor,
        tagValue: element.propertyLabel,
        tagKey: element.propertyValue,
        fillColor: element.fillColor,
        boxColorOpacity: element.boxColorOpacity,
        fillColorOpacity: element.fillColorOpacity
      };

      dataArrary.push(data)
    });
    return dataArrary;
  },
  //获取表单配置数据
  getFormListData() {
    let dataArrary = [];
    window.getData.formData.propertyLst.forEach(element => {
      if (element.propertyId === 0) {
        return;
      }
      console.log(element);
      let data = {
        propertyKey: element.propertyKey,
        type: element.propertyType == 1 ? 'radio' : element.propertyType == 2 ? 'checkbox' : 'text',
        propertyName: element.propertyName,
        groupId: element.groupId
      }
      let valueArrary = [];
      if (!element.propertyValueLst) {
        dataArrary.push(data);
        return
      }
      element.propertyValueLst.forEach(item => {
        let data = {
          propertyValue: item.propertyValue,
          propertyLabel: item.propertyLabel
        }
        valueArrary.push(data);
      })
      data.propertyValuelst = valueArrary;
      dataArrary.push(data);
    })
    return dataArrary;
  },
  //获取全局配置
  getFoundationConfig() {
    let cache = {};
    window.getData.formData.foundationConfig.forEach(element => {
      cache[element.configName] = element.configValue;
    })
    return cache;
  }
}


