<!-- 标签列表 组件 -->

<template>
  <div class="tool-bar" :class="toolBarFlag?'':'tool-bar-hide'">
    <span class="arrow" @click="toolBarHide">
      <img :src="toolBarFlag?require('../assets/images/u151.png'):require('../assets/images/u170.png')" alt="">
    </span>
    <ul :class="toolBarFlag?'':'hide'">
      <li class="index" @click="drawRect">
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u159.png')+')'}"></i>
        <span>矩形</span>
      </li>
      <li class="index" @click='select'>
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u166.png')+')'}"></i>
        <span>选择</span>
      </li>
      <!-- <li class="index" @click='and'>
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u96.png')+')'}"></i>
        <span>组合</span>
      </li> -->
      <li class="index" @click='zoomIn'>
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u96.png')+')'}"></i>
        <span>放小</span>
      </li>
      <li class="index" @click='zoomOut'>
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u91.png')+')'}"></i>
        <span>放大</span>
      </li>
      <li class="index" @click='reset'>
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u137.png')+')'}"></i>
        <span>重新标注</span>
      </li>
      <li class="index" @click="shapeHide">
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u25.png')+')'}"></i>
        <span>图形可见</span>
      </li>
      <li class="index" @click="labelHide">
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u101.png')+')'}"></i>
        <span>标签可见</span>
      </li>
      <li class="index" @click="goHome">
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u111.png')+')'}"></i>
        <span>Home</span>
      </li>
      <li class="index" @click="currentShape">
        <i class="ico-index" :style="{backgroundImage:'url('+require('../assets/images/u116.png')+')'}"></i>
        <span>当前图形</span>
      </li>
    </ul>
  </div>

</template>

<script>
export default {
  data() {
    return {
      shapeFlag: true,
      labelFlag: true,
      toolBarFlag: true
    };
  },
  created() {},
  mounted() {
    //toolBar快捷键
    document.addEventListener("keydown", e => {
      console.log(e);
      if (e.ctrlKey) {
        switch (e.code) {
          case "KeyV":
            e.preventDefault();
            this.shapeHide();
            break;
          case "KeyO":
            e.preventDefault();
            this.labelHide();
            break;
          case "Equal":
            e.preventDefault();
            this.zoomOut();
            break;
          case "Minus":
            e.preventDefault();
            this.zoomIn();
            break;
          case "KeyF":
            e.preventDefault();
            this.goHome();
            break;
          case "KeyR":
            e.preventDefault();
            this.reset();
            break;
          case "KeyC":
            e.preventDefault();
            this.currentShape();
            break;
        }
      }
    });
  },
  props: {},

  methods: {
    shapeHide() {
      this.$emit("shapeHide", this.shapeFlag);
      this.shapeFlag = !this.shapeFlag;
    },
    labelHide() {
      this.$emit("lableHide", this.labelFlag);
      this.labelFlag = !this.labelFlag;
    },
    toolBarHide() {
      this.toolBarFlag = !this.toolBarFlag;
    },
    and() {
      this.$emit("and");
    },
    drawRect() {
      this.$emit("drawRect");
    },
    select() {
      this.$emit("select");
    },
    zoomIn() {
      this.$emit("zoomIn");
    },
    zoomOut() {
      this.$emit("zoomOut");
    },
    reset() {
      this.$emit("reset");
    },
    goHome() {
      this.$emit("goHome");
    },
    currentShape() {
      this.$emit("currentShape");
    }
  }
};
</script>

<style scoped>
.tool-bar {
  position: absolute;
  height: 50px;
  width: 960px;
  bottom: 0px;
  left: 50%;
  background: red;
  transform: translate(-50%, 0);
  background: rgba(49, 62, 84, 0.2);
}
.tool-bar-hide {
  height: 15px;
}
.tool-bar li {
  float: left;
  margin-left: 30px;
}
.tool-bar li i {
  display: block;
  width: 28px;
  height: 28px;
  background: no-repeat;
  background-size: 25px auto;
  background-position: 0 4px;
  margin: 0 auto;
}
.hide {
  display: none;
}

.tool-bar li span {
  color: rgb(145, 155, 162);
}
.arrow {
  position: absolute;
  top: 0px;
  right: 0;
}
.arrow img {
  height: 18px;
  width: 18px;
}
</style>



