var uid = 1

/**
 * 序列生产器
 *
 * @return {number}
 */
export function getUid(featureLayer) {
  var features = featureLayer.features

  if (features.length === 0) {
    return uid++
  }

  var maxValue = Number.MIN_VALUE
  features.forEach(function (f) {
    if (f.get('objectId')) {
      maxValue = Math.max(maxValue, Number(f.get('objectId')))
    }
  })

  if (maxValue < 1) {
    return 1
  }

  return maxValue + 1
}


