let flayer = null;
const shapeList = {
  namespaced: true,
  state: {
    shapeList: [],//图形列表数据
  },
  mutations: {
    INIT: (state, data) => {
      flayer = data.flayer;
      data.features.forEach(feature => {
        state.shapeList.push({
          id: feature.get('objectId'),
          tagValue: feature.get("label").propertyName,
          currentFeature: feature
        })
      })
    },
    ADD: (state, data) => {
      let isRepeat = false;
      state.shapeList.forEach(ele => {
        if (ele.id == data.shapeInfo.id) {
          isRepeat = true;
        }
      })
      !isRepeat && state.shapeList.push(data.shapeInfo);
    },
    DELETE: (state, shapeInfo) => {
      state.shapeList.forEach((element, index) => {
        if (element.id === shapeInfo.id) {
          state.shapeList.splice(index, 1);
        }
      });
    },
    CLEAR: (state) => {
      state.shapeList = [];
    },
    MODIFY: (state, data) => {
      state.shapeList.forEach((element, index) => {
        data.features.forEach(ele => {
          if (element.currentFeature.id == ele.id) {
            state.shapeList[index].tagValue = data.tagValue;
            state.shapeList[index].currentFeature = ele;
          }
        })
      });
    }
  },
  actions: {
    init({ commit }, data) {
      commit("INIT", data)
    },
    add({ commit }, data) {
      commit('ADD', data);
      if (!flayer) {
        flayer = data.flayer
      }
    },
    delete({ commit }, shapeInfo) {
      commit('DELETE', shapeInfo);
      flayer.removeFeature(shapeInfo.currentFeature);
    },
    clear({ commit }) {
      commit('CLEAR');
    },
    modify({ commit }, data) {
      commit("MODIFY", data);
    }
  }
};

export default shapeList;



